import * as React from "react"

// Import components
import MyNav from "./MyNav"
import ChooseMajor from "./- ChooseMajor"
import CumGPA from "./CumGPA"

// Import styling
import "bootstrap/dist/css/bootstrap.min.css"
import "./style.css"
import { ChakraProvider, SimpleGrid, Box } from "@chakra-ui/react"
import { useLocalStorage } from "react-use"
import { Container, Form, Button, Table as t } from "react-bootstrap"
import {
  Radio,
  RadioGroup,
  Stack,
  Button as B,
  Divider,
  Table,
  Thead,
  Tbody,
  Tfoot,
  Alert,
  AlertIcon,
  AlertTitle,
  AlertDescription,
  Tr,
  Th,
  Td,
  TableCaption,
  TableContainer,
  DeleteIcon,
} from "@chakra-ui/react"

// Import linechart
import { Line } from "react-chartjs-2"
import {
  Chart as ChartJS,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
} from "chart.js"
import { addPointerInfo } from "framer-motion"

ChartJS.register(LineElement, CategoryScale, LinearScale, PointElement)

function Index() {
  const [selectedMajor, setSelectedMajor] = React.useState("Computer-Science")

  const [semestersList, setSemestersList] = useLocalStorage("semestersList", [])

  const handleSetSelectedMajor = major => {
    setSelectedMajor(major)
  }

  const [JSONSubjects, setJSONSubjects] = React.useState([])

  const [semester, setSemester] = React.useState("2021/1")

  const semesters = [
    "2021/1",
    "2021/2",
    "2022/1",
    "2022/2",
    "2023/1",
    "2023/2",
    "2024/1",
    "2024/2",
  ]

  const [subjectArr, setSubjectArr] = useLocalStorage("subjectArr", [])

  const [showAlert, setShowAlert] = React.useState(false)

  function displayAlert() {
    setShowAlert(true)
  }

  function notDisplayAlert() {
    setShowAlert(false)
  }

  function handleAddToArr(semester, code, course, credit, grade) {
    let checkSubjectExist = subjectArr.some(sub => sub[2] === course)
    if (checkSubjectExist) {
      // showAlert()
      displayAlert()
    } else {
      subjectArr.push([semester, code, course, credit, grade])
      console.table(subjectArr)
      setSubjectArr([...subjectArr])
      calculateCumGPA()
      notDisplayAlert()
    }
  }

  const [selectedGroup, setSelectedGroup] = useLocalStorage("selectedGroup", [])

  const [grade, setGrade] = React.useState("F")

  const [selectedSubject, setSelectedSubject] = React.useState([])

  const [selectedGradeValue, setSelectedGradeValue] = React.useState(0)
  const [cumGpa, setCumGpa] = useLocalStorage("cumulativeGpa", 0.0)
  const [totalCredits, setTotalCredits] = useLocalStorage("totalCredit", 0.0)

  const grades = {
    W: 0,
    F: 1,
    D: 1.25,
    "C-": 1.75,
    C: 2,
    "C+": 2.25,
    "B-": 2.75,
    B: 3,
    "B+": 3.5,
    "A-": 3.75,
    A: 4,
  }

  function calculateCumGPA() {
    let subjectList = JSON.parse(localStorage.getItem("subjectArr"))
    let totalPoints = 0
    let totalCredits = 0

    subjectList.forEach(data => {
      let grade = data[4]
      let credits = data[3]
      if (grade === "W") {
        return
      }
      totalPoints += grades[grade] * credits
      totalCredits += credits
    })

    let gpa = (totalPoints / totalCredits).toFixed(2)
    setCumGpa(gpa)
    setTotalCredits(totalCredits)
  }

  const handleChange = event => {
    setGrade(event)
    setSelectedGradeValue(grades[event])
  }

  React.useEffect(() => {
    const importJson = async () => {
      const importedJson = await import(`./${selectedMajor}.json`)
      setJSONSubjects(importedJson.curriculum.subjects)
      console.log({ selectedMajor })
      console.log("New subjects imported for ", { selectedMajor })
    }
    importJson()
  }, [selectedMajor])

  React.useEffect(() => {
    if (selectedGroup.subjects) {
      setSelectedSubject(selectedGroup.subjects[0])
    }
  }, [selectedGroup])

  let groupedSubjectArr = {}

  subjectArr.forEach(subject => {
    let key = subject[0]
    if (!groupedSubjectArr[key]) {
      groupedSubjectArr[key] = []
    }
    groupedSubjectArr[key].push(subject)
  })

  let semList = []
  let semGpaList = []

  function calculateSemGpa(subjects) {
    let semPoint = 0
    let semCredit = 0

    subjects.forEach(subject => {
      let credit = subject[3]
      let grade = subject[4]
      if (grade === "W") {
        return
      }
      semCredit += credit
      semPoint += grades[grade] * credit
    })

    let semGpa = (semPoint / semCredit).toFixed(2)
    return semGpa
  }

  Object.keys(groupedSubjectArr).forEach(key => {
    semList.push(key)
    semGpaList.push(calculateSemGpa(groupedSubjectArr[key]))
  })

  console.log(semList)
  console.log(semGpaList)

  const data = {
    labels: semList,
    datasets: [
      {
        data: semGpaList,
        borderColor: "rgb(53, 162, 235)",
        backgroundColor: "rgba(53, 162, 235, 0.5)",
        cubicInterpolationMode: "monotone",
      },
    ],
  }

  function deleteCourse(row) {
    let newArr = [...subjectArr]
    let index = subjectArr.findIndex(code => code[1] === row)
    if (index === -1) {
      return
    }
    newArr.splice(index, 1)
    setSubjectArr(newArr)
    calculateCumGPA()
  }

  return (
    <ChakraProvider>
      <Container className="bg">
        <div className="body">
          <MyNav />

          <ChooseMajor
            handleSelectMajor={handleSetSelectedMajor}
            major={selectedMajor}
          />

          <SimpleGrid columns={2} spacing={10}>
            <Box bg="#f1f5f8" height="250px">
              <div className="chart">
                <Line data={data}></Line>
              </div>
            </Box>
            <Box bg="#f1f5f8" height="250px">
              {" "}
              <CumGPA cumGpa={cumGpa} totalCredits={totalCredits} />
            </Box>
          </SimpleGrid>

          <div className="semesters">
            <TableContainer>
              <Table size="lg" variant="striped" colorScheme="twitter">
                <TableCaption>Added Courses</TableCaption>

                <Thead>
                  <Tr>
                    <Th>Semester</Th>
                    <Th>Course Code</Th>
                    <Th>Course</Th>
                    <Th>Credit</Th>
                    <Th>Grade</Th>
                    <Th>&nbsp;</Th>
                  </Tr>
                </Thead>
                <Tbody>
                  {subjectArr.map((subject, index) => (
                    <Tr key={index}>
                      <Td>{subject[0]}</Td>
                      <Td>{subject[1]}</Td>
                      <Td>{subject[2]}</Td>
                      <Td>{subject[3]}</Td>
                      <Td>{subject[4]}</Td>
                      <Td>
                        <img
                          src="https://cdn-icons-png.flaticon.com/512/1214/1214428.png"
                          alt="delete icon"
                          style={{
                            width: "20px",
                            marginBottom: "0em",
                          }}
                          onClick={() => deleteCourse(subject[1])}
                        />
                      </Td>
                    </Tr>
                  ))}
                </Tbody>
              </Table>
            </TableContainer>
            <br />
            {showAlert && (
              <Alert status="error">
                <AlertIcon />
                Duplicated course! Please select another course and try again.
              </Alert>
            )}
            <br />

            <h6> Select Semester:</h6>
            <Form.Select
              aria-label="Default select example"
              value={semester}
              onChange={e => setSemester(e.target.value)}
            >
              {semesters.map(sem => (
                <option key={sem} value={sem}>
                  {sem}
                </option>
              ))}
            </Form.Select>
            <br />
            <h6> Select Subject Group: </h6>
            <Form.Select
              aria-label="Default select example"
              value={selectedGroup.groupName}
              onChange={e =>
                setSelectedGroup(
                  JSONSubjects.find(group => group.groupName === e.target.value)
                )
              }
            >
              <option>Select a group</option>
              {JSONSubjects.map(group => (
                <option key={group.groupName} value={group.groupName}>
                  {group.groupName}
                </option>
              ))}
            </Form.Select>
            <br />
            <h6> Select Subject: </h6>
            <Form.Select
              aria-label="Default select example"
              value={selectedSubject.name}
              onChange={e =>
                setSelectedSubject(
                  selectedGroup.subjects.find(
                    subject => subject.name === e.target.value
                  )
                )
              }
            >
              {selectedGroup && selectedGroup.subjects
                ? selectedGroup.subjects.map(subject => (
                    <option key={subject.code} value={subject.name}>
                      {subject.name}
                    </option>
                  ))
                : null}
            </Form.Select>
            <br />
            <h5> Select Grade: </h5>
            <RadioGroup value={grade} onChange={handleChange}>
              <Stack spacing={7} direction="row">
                <Radio colorScheme="twitter" size="lg" value="W">
                  W
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="F">
                  F
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="D">
                  D
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="C-">
                  C-
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="C">
                  C
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="C+">
                  C+
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="B-">
                  B-
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="B">
                  B
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="B+">
                  B+
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="A-">
                  A-
                </Radio>
                <Radio colorScheme="twitter" size="lg" value="A">
                  A
                </Radio>
              </Stack>
            </RadioGroup>
            <br />
            <B
              colorScheme="twitter"
              onClick={() =>
                handleAddToArr(
                  semester,
                  selectedSubject.code,
                  selectedSubject.name,
                  selectedSubject.credit,
                  grade
                )
              }
            >
              Add Course
            </B>
            <br />
            <br />
            <Divider />
            <br />
            <br />
          </div>

          {/* <SemesterList
            semesters={semesters}
            semester={semester}
            setSemester={setSemester}
            setSemestersList={setSemestersList}
            semestersList={semestersList}
            subArr={subjectArr}
            setSubArr={setSubjectArr}
            JSONsubjects={JSONSubjects}
          /> */}
        </div>
      </Container>
    </ChakraProvider>
  )
}

export default Index
